import React from "react";
import { storiesOf } from "@storybook/react";
import { BrandContext } from "../../BrandContext";
import { UserDataProviderCustom } from "../../BrandContext/BrandContextProvider"
import ThemeProviderCustom from "../../ThemeProviderCustom";
import mockBrands from "../../utils/brandMock"
import Header from "../Header";

const HeaderStory = ({ brand }) => (
    <BrandContext.Provider value={mockBrands[brand]}>
        <ThemeProviderCustom>
            <UserDataProviderCustom>
                <Header />
            </UserDataProviderCustom>
        </ThemeProviderCustom>
    </BrandContext.Provider>
);

const argsControls = {
    args: {
        brand: "sub",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["cvc", "cvccorp", "sub"]
        },
    }
}

storiesOf("Components/Header", module)
    .add("Default", (args) => <HeaderStory {...args} />, argsControls);
